import React, {useState, useEffect} from "react";
import {Modal, Form, FormControl, CloseButton, Container} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faWhatsapp} from "@fortawesome/free-brands-svg-icons";
import {faUserCircle, faEnvelope} from "@fortawesome/free-regular-svg-icons";
import {faClipboardList} from "@fortawesome/free-solid-svg-icons";

export function SidePanel(props) {
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [validated, setValidated] = useState(false);

    const handleSubmit = (event) => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }

        setValidated(true);
    };

    function submitQuery(event){

        event.currentTarget.checkValidity();
        let data = {
            "name":name,
            "email":email,
            "phone":phone,
            "query":Query
        }


        console.log(data)
    }

    let postDate = (props.date?.getDate()) + "-" + (props.date?.getMonth() + 1) + "-" + (props.date?.getFullYear());

    let [name,changeName] = useState("");
    let [email,changeEmail] = useState("");
    let [phone,changePhone] = useState("");
    let [Query,changeQuery] = useState("");


    return (
        <div>
            <Modal show={show} animation={true} centered>
                <Modal.Body>
                    <CloseButton onClick={handleClose} aria-label="Close"/>
                    <Modal.Title className="text-center mb-5">Write your query</Modal.Title>

                    <div className="appointment-form mx-4">
                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <div>
                                <FontAwesomeIcon icon={faUserCircle} className="form-icon"/>
                                <Form.Control
                                    type="text"
                                    placeholder="Name"
                                    value={name}
                                    required
                                    onChange={(event)=>changeName(event.target.value)}/>
                            </div>

                            <div>
                                <FontAwesomeIcon icon={faEnvelope} className="form-icon"/>
                                <Form.Control
                                    type="email"
                                    placeholder="Email"
                                    value={email}
                                    required
                                    onChange={(event)=>changeEmail(event.target.value)}/>
                            </div>

                            <div>
                                <FontAwesomeIcon icon={faWhatsapp} className="form-icon"/>
                                <Form.Control
                                    type="text"
                                    placeholder="Phone(WhatsApp preferred)"
                                    value={phone}
                                    required
                                    onChange={(event)=>changePhone(event.target.value)}/>
                            </div>

                            <div>
                                <FontAwesomeIcon icon={faClipboardList} className="form-icon ta-form-icon"/>
                                <Form.Control
                                    as="textarea"
                                    rows="4" cols="45"
                                    placeholder="Query"
                                    value={Query}
                                    required
                                    onChange={(event)=>changeQuery(event.target.value)}/>
                            </div>
                            <button type="submit" className="proceed modal-proceed" >Submit</button>

                        </Form>
                    </div>
                </Modal.Body>
                <Modal.Footer className="form-footer">
                </Modal.Footer>
            </Modal>

           <Container>
               <div className="Slots">
                   <h2>
                       {postDate}
                   </h2>
                   <div className="timings mx-4">
                       {props.data != null && props.data[postDate] != null
                           ? props.data[postDate][0]?.map((slot) => (
                               <button className={`available-slots ${slot.id === props.selectedSlotId?"selected-slot":""}`}
                                       onClick={() => {
                                   props.changeSlot(slot.id)

                               }}>
                                   {slot.start_time}-{slot.end_time}
                               </button>
                           ))
                           : "No Slots available"}
                   </div>
                   <div>
                       <button className="proceed slot-proceed mt-3" disabled={props.selectedSlotId===null} id="proceed"
                               onClick={handleShow}>
                           Proceed
                       </button>
                   </div>
               </div>
           </Container>
        </div>
    );
}
