// Main App
function App(props) {
    let [selectedDate, setSelectedDate] = useState(new Date());
    let updateSelectedDate = (date) => {
        setSelectedDate(date)
    }
    const [data, setData] = useState(null);
    let [selectedSlotId, changeSlotId] = useState(null);

    let changeSlot = (slot) => {
        changeSlotId(slot);
    }

    useEffect(() => {
        const requestOptions = {
            method: "GET"
        };
        fetch(
            "https://dev.thegrowingdeveloper.org/slots/getAvailableSlots",
            requestOptions
        )
            .then((response) => response.json())
            .then((jsonRes) => {
                setData(jsonRes);
            })
            .catch((err) => console.log(err));
    }, []);
    return (
        <div className="App">

            {/* Render Navbar */}
            <Topbar/>

            {/* Render Banner */}
            <section>
                <Banner/>
            </section>

            {/* Render Calender */}
            <div className="main-container">
                <div className="container mx-auto">
                    <Row>
                        <Col lg={7}>
                            {
                                data !== null ?
                                    <Calendar selectedDate={selectedDate} updateSelectedDate={updateSelectedDate}
                                              selectedSlotId={selectedSlotId} changeSlot={changeSlot}
                                              data={data}/> : ""}
                        </Col>

                        <Col
                            lg={5}
                            className="d-flex align-items-center justify-content-center mt-5"
                        >
                            <SidePanel date={selectedDate} data={data} selectedSlotId={selectedSlotId}
                                       changeSlot={changeSlot}/>
                        </Col>
                    </Row>
                </div>
            </div>

            <div className="promotion">
                <Container>
                    <Row className="">
                        <Col md={6}>
                            <BuyApi/>
                        </Col>

                        <Col md={6}>
                            <Row>
                                <Contact className="Contact"/>
                            </Row>

                            <Row>
                                <Youtube/>
                            </Row>
                        </Col>
                    </Row>
                </Container>
            </div>

            {/* Render Footer */}
            <Footer/>

            {/* <PaymentSuccess />

      <APIcomponent /> */}
        </div>
    );
}

export default App;
